import { observable, computed, action } from 'mobx';

export class Store {
    @observable private enthusiasmLevel: number = 1;

    @action increment() {
        this.enthusiasmLevel += 1;
    }

    @action decrement() {
        this.enthusiasmLevel -= 1;
    }

    @computed get current(): number {
        return this.enthusiasmLevel;
    }
}

export default Store;



