import * as React from 'react';
import { Helmet } from 'react-helmet';

import { Link } from 'react-router-dom';

import Metadata from './Metadata';

export interface State {
    documents: Metadata[];
}

class Index extends React.Component<{}, State> {
    componentWillMount() {
        const readmePath = require('../../blog/blog-list.jsn');
        fetch(readmePath)
            .then(response => response.text())
            .then(text => {
                const documents = JSON.parse(text) as Metadata[];
                this.setState({ documents: documents });
            });
    }

    render() {
        if (this.state) {
            return (
                <section>
                    <Helmet>
                        <meta charSet="utf-8" />
                        <title>BlogIndex</title>
                    </Helmet>
                    {this.state.documents.map(d => <Link key={d.name} to={'/blog/' + d.name}>{d.title}</Link>)}
                </section>
            );
        } else {
            return (
                <div>
                    Loading...
                </div>
            );
        }
    }
}

export default Index;



