import * as React from 'react';

import { BrowserRouter, Link, Route, Switch, RouteProps } from 'react-router-dom';
import { Helmet } from 'react-helmet';

import DevTools from 'mobx-react-devtools';

import Responsive from 'react-responsive';

import Hello from './Hello';
import NotFound from './NotFound';
import BlogIndex from './blog/Index';
import BlogEntry from './blog/Entry';
import Search from './Search';

import { StoreContext, ViewContext } from './Context';

import PlacesExplorer from './PlacesExplorer';

import Store from './Store';
const store = new Store();

interface DeviceInfo {
    size: 'desktop' | 'tablet' | 'mobile' | 'default';
}

const ResponsiveApp = (device: DeviceInfo) => (
    <StoreContext.Provider value={store}>
        <ViewContext.Provider value={{ view: 'aaa' }}>
            <Helmet>
                <meta charSet="utf-8" />
                <title>My Title 55</title>
                <link rel="canonical" href="http://mysite.com/example" />
            </Helmet>
            <BrowserRouter>
                <>
                    {process.env.NODE_ENV === 'development' ? <DevTools /> : null}
                    {/* <nav>
                    <Link to="/">Map</Link>
                    <Link to="/greeting" className="greetingLink">Greeting</Link>
                    <Link to="/blog">Blog Index</Link>
                    <Link to="/search">Search</Link>
                </nav> */}
                    <Switch>
                        <Route
                            exact={true}
                            strict={true}
                            path="/"
                            render={() => <PlacesExplorer />}
                        />
                        <Route
                            path="/greeting"
                            render={(routeProps: RouteProps) => <Hello name="Andrey" store={store} />}
                        />
                        <Route exact={true} strict={true} path="/blog" component={BlogIndex} />
                        <Route exact={true} strict={true} path="/blog/:name" component={BlogEntry} />
                        <Route exact={true} strict={true} path="/search" component={Search} />
                        <Route path="*" component={NotFound} />
                    </Switch>
                </>
            </BrowserRouter>
        </ViewContext.Provider>
    </StoreContext.Provider>
);

const App = () => (
    // todo capture device orientation, use sizes from bootstrap,
    // is resolution matter, retina for example? maybe for pics
    <>
        <Responsive minWidth={992} ><ResponsiveApp size={'desktop'}/></Responsive>
        <Responsive minWidth={768} maxWidth={991}><ResponsiveApp size={'tablet'} /></Responsive>
        <Responsive maxWidth={767}><ResponsiveApp size={'mobile'} /></Responsive>
        <Responsive maxWidth={768}><ResponsiveApp size={'default'} /></Responsive>
    </>
);

export default App;



