import './Hello.css';

import * as React from 'react';
import { observer } from 'mobx-react';

import Store from './Store';

import PlacesList from './PlacesList';

import * as L from 'leaflet';
import 'leaflet/dist/leaflet.css';

export interface Props {
    store: Store;
}

export interface State {
    map: L.Map;
}

class InteractiveMap extends React.Component<{}, {}> {

    private map: L.Map;

    componentDidMount() {
        const state = {
            lat: 51.505,
            lng: -0.09,
            zoom: 13,
        };
        const position = new L.LatLng(state.lat, state.lng);
        const attributionUrl = 'https://openstreetmap.org/copyright';
        const attribution = `&amp;copy <a href=&quot;${attributionUrl}&quot;>OpenStreetMap</a> contributors`;
        this.map = L.map('map', {
            center: position,
            zoom: state.zoom,
            layers: [
                L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
                    attribution: '' // TODO disable while react-snap fails attribution
                }),
            ]
        });
    }

    render() {
        return <div id="map" style={{ height: '100%' }} />;
    }
}

export default InteractiveMap;



