import * as marked from 'marked';
import * as frontmatter from 'front-matter';
import * as React from 'react';
import { RouteComponentProps, Redirect } from 'react-router-dom';
import { Helmet } from 'react-helmet';

import Attributes from './Attributes';
import Document from './Document';

export interface Props {
    name: string;
}

export default class Entry extends React.Component<RouteComponentProps<Props>, Document> {
    componentWillMount() {
        let readmePath = '';
        try {
            readmePath = require('../../blog/' + this.props.match.params.name + '.md');
        } catch (error) {
            this.setState({ content: '', title: '', name: '' } as Document);
            return;
        }
        fetch(readmePath)
            .then(response => {
                return response.text();
            }).then(text => {
                const extracted = frontmatter(text);
                const state = Object.assign(
                    { name: this.props.match.params.name, content: marked(extracted.body) },
                    extracted.attributes as Attributes) as Document;
                this.setState(state);
            });
    }

    render() {
        if (this.state) {
            if (this.state.name !== '') {
                return (
                    <section>
                        <Helmet>
                            <meta charSet="utf-8" />
                            <title>{this.state.title}</title>
                        </Helmet>
                        <article dangerouslySetInnerHTML={{ __html: this.state.content }} />
                    </section>
                );
            } else {
                return <Redirect to={'/not_found?blogname=' + this.props.match.params.name}/>;
            }
        } else {
            return (
                <div>
                    Loading...
                </div>
            );
        }
    }
}



