import * as React from 'react';
import { Helmet } from 'react-helmet';
// import { Link } from 'react-router-dom';

import * as lunr from 'lunr';

export interface State {
    index: lunr.Index;
}

class Search extends React.Component<{}, State> {
    lunrIndex = require('../blog/blog-index.jsn');

    componentWillMount() {
        fetch(this.lunrIndex).then(response => {
            return response.text();
        }).then(text => {
            const index = lunr.Index.load(JSON.parse(text));
            this.setState({ index: index });
        });
    }

    render() {
        if (this.state) {
            return (
                <section>
                    <Helmet>
                        <meta charSet="utf-8" />
                        <title>Search Index</title>
                    </Helmet>
                    {this.state.index.search('text')[0].ref}
                </section>
            );
        } else {
            return (
                <div>
                    Loading...
                </div>
            );
        }
    }
}

export default Search;



